#playlist-name {
  flex-grow: 2;
  border: 0;
  outline: 0;
  background-color: purple;
  border-bottom: 1px solid #6f6f6f;
  font-family: "Poppins", sans-serif;
  font-size: 1.55rem;
  font-weight: bold;
  color: #000;
  margin-right: 0.5em;
  border-radius: 0;
}

.Playlist-input {
  display: flex;
  margin: 0em 0.75em;
  margin-top: 0.5em;
  position: sticky;
  top: 0;
}

.Playlist-Clear {
  flex-grow: 1;
  position: sticky;
  bottom: 0;
  background-color: purple;
}

