.Track {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  margin: 0em 1em;
  align-items: center;
  border-bottom: 1px solid black;
  padding: 0em 0.7em;
}

.Track-image img {
  border-radius: 24px;
}

/* .Track-add, */
.Track-info {
  border-radius: 25px;
  padding: 0.2em;
}
.Track-add {
  text-align: center;
  min-width: 20%;
}

.Track-add button {
  border-radius: 25px;
  padding: 0.5em;
  min-width: 100%;
}

.Track-info {
  width: 70%;
  padding: 10px;
  text-align: left;
}

#artist {
  font-size: smaller;
}
