body,
#root,
.App {
  height: 100%;
}

.Header {
  margin-top: 0.5em;
  margin-bottom: 0.5em;
}

.Main-Container {
  height: 100%;
}

.SearchContainer {
  width: 100%;
  margin: 0.5em 1em;
}

.ListsContainer {
  width: 100%;
  min-height: 40%;
  max-height: 60%;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: stretch;
  margin: 5px;
}

.SearchResultsContainer,
.AddedToPlaylistContainer {
  display: flex;
  justify-content: center;
  width: 45%;
  padding-bottom: 10px;
}

.SearchResults,
.Playlist {
  background-color: purple;
  border-radius: 24px;
  min-height: 100%;
  flex-grow: 1;
  overflow: scroll;
}
