.SearchBar {
  height: 40%;
}

.SearchBar-input {
  margin-bottom: 1em;
  height: 100%;
}

input {
  width: 30%;
  padding: 10px 20px;
  border-radius: 24px;
}

button {
  border-radius: 24px;
  padding: 1em 2em;
  background-color: purple;
}

button:hover {
  background-color: pink;
  cursor: pointer;
}
